/* reset */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video, input,textarea, select, button
{ margin: 0; padding: 0; border: 0; vertical-align: baseline; box-sizing:border-box; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display: block;}
ol, ul {list-style: none;}
blockquote, q {quotes: none;}
blockquote:before, blockquote:after, q:before, q:after {content:""; content: none;}
table {border-collapse: collapse; border-spacing: 0; }
table th{text-align:left;}
table td, 
table th{word-break:break-all; word-wrap: break-word;}
form, fieldset{border:none;}
img{vertical-align:top; border:none; max-width:100%;}
input, select{vertical-align:middle; -webkit-appearance:none; appearance:none;}
select::-ms-expand {display:none}
legend, caption{visibility:hidden; overflow:hidden; width:0; height:0; font-size:0; line-height:0; margin:0; padding:0;}
h1, h2, h3, h4, h5, h6{font-weight:normal; font-style:normal; }
em, address{font-weight:normal; font-style:normal; }
hr{display:none; }
button{vertical-align:middle; cursor:pointer;border:none; background-color:transparent; outline:none}
.blind{ visibility:hidden; position:absolute; font-size:0; width:0; height:0; line-height:0; margin:0; padding:0; overflow:hidden}

a{color: #111; text-decoration:none;}
a:hover{ text-decoration:none;} 

html, body{width:100%;}
html {font-size:10px;}
body, a, button, input, select, textarea{font-family:Blorado, sans-serif}  
body{font-size:1.4rem; font-weight:400; line-height:1.2; background:#fff; width:100%; height:100%; color:#111} 


/* --------------common-------------- */
.container {margin:0 auto; width:min(1024px, 100%); padding:0 5%;}
.header {padding:20px 0;}
.navigate {display: flex; justify-content: flex-end; gap:30px; font-size:1.6rem}
.title {font-weight:bold; font-size:3.6rem; padding:2rem 0;}
.switch-wrap {display:flex; justify-content: flex-end; gap:10px; align-items: center; padding:5px 0 0;}
.switch {display:block; width:44px; height:25px; background:blue; box-shadow:inset -2px -2px 5px rgba(0,0,0,.5); border-radius:30px; padding:3px;}
.switch .circle {display:block; width:50%; height:100%; border-radius:50%; background:gray; box-shadow:2px 2px 5px rgba(0,0,0,.5); transition: all .3s;}
.switch.on .circle {margin-left:50%;}
.tags {display:flex; justify-content:flex-start; gap:3px; flex-wrap:wrap;}
.tag {display:inline-block; padding:2px 10px 4px; border-radius:30px; background: #666666; color:#fff;}


/* --------------project-------------- */
.pj {display:flex; justify-content:flex-start; flex-wrap:wrap; gap:20px;}
.pj li {width:calc((100% - 40px)/3); border-radius:15px; overflow:hidden; box-shadow: 0px 0px 15px rgba(0,0,0,.2);}
.pj a {display:block; width:100%; padding-top:70%; background-size: cover; background-position: center top; position:relative;}
.pj a .cover {position:absolute; width:100%; height:100%; background:#000; opacity:0; top:0; left:0; transition: opacity .5s;}
.pj a .desc {position:absolute; width:100%; padding:0 max(6%, 15px) max(6%, 15px); left:0; bottom:0; color:#fff; opacity:0; transform:translateY(30%); transition: all .5s;}
.pj .desc .pro-tit {margin-bottom:20px; font-weight:bold; font-size:2.4rem;}

/* --------------modal-------------- */
.modal-cover {position:fixed; width:100%; height:100%; top:0; left:0; background-color:rgba(0,0,0,0.7); z-index:10;}
.modal-cover .modal-wrap {height:100%; background:#fff; position:absolute; top:0; right:0; width:min(570px, calc(100% - 100px)); padding:30px min(30px, 7%) 100px; overflow-y: scroll; animation: Modal 0.5s ease-in-out;}

@keyframes Modal {
	0% {
		transform:translateX(100%)
	}
	100% {
		transform:translateX(0)
	}
}

.img-wrap {width:100%; margin:0 auto; padding-top:60%; border-radius: 15px; background-position-x: center; background-position-y: 0%; background-repeat: no-repeat; background-size: cover; box-shadow: 0px 0px 15px rgba(0,0,0,.2); transition: all .5s;}
.img-wrap.run {animation:imgMove 7s 1s ease-in-out alternate infinite;}

@keyframes imgMove {
	0% {
		background-position-y: 0%;
	}
	2% {
		background-position-y: 0%;
	}
	98% {
		background-position-y: 100%;
	}
	100% {
		background-position-y: 100%;
	}
}



@media all and (min-width: 769px){
	.pj a:hover .cover {opacity:0.7;}
	.pj a:hover .desc {opacity:1; transform:translateY(0);}
}/* only PC */

@media screen and (max-width:768px) {
	.pj {gap:15px;}
	.pj li {width:calc((100% - 15px)/2);}
	.pj a .cover {opacity:0.7;}
	.pj a .desc {opacity:1; transform:translateY(0);}
}/* (max-width:768px) */

@media screen and (max-width:480px) {
	html {font-size:9px;}
	.pj li {width:100%;}

	.modal-cover .modal-wrap {width:100%;}
}/* (max-width:480px) */